import React,{ useEffect, useState } from 'react'
import SideBarMobile from '../../../Global/App/User/SideBarMobile.component'
import {Line} from 'react-chartjs-2';

const DashboardSection=()=> {
	const data ={
		labels:['January', 'February', 'March', 'April', 'May', 'June', 'July'],
		datasets: [{
			label: 'Dataset',
			borderColor: '#FF6384',
			backgroundColor: '#FF6384',
			data: [10, 30, 46, 2, 8, 50, 0],
			fill: false,
		}]
	}
	const Count = ({ number }) => {

  const [count, setCount] = useState("0")
  useEffect(() => {
    let start = 0;
    let duration='2'
    const end = parseInt(number.substring(0,2))
    if (start === end) return;
    let totalMilSecDur = parseInt(duration);
    let incrementTime = (totalMilSecDur / end) * 1000;
    let timer = setInterval(() => {
      start += 1;
      setCount(String(start) + number.substring(2))
      if (start === end) clearInterval(timer)       
    }, incrementTime);
  }, [])

  return (
	  	<div className="timer">
			{count}
		</div>
  )}
    return (
        <section className="our-dashbord dashbord bgc-f7 pb50">
		<div className="container-fluid">
			<div className="row">
				<div className="col-lg-3 col-xl-2 dn-992 pl0"></div>
				<div className="col-lg-9 col-xl-10 maxw100flex-992">
					<div className="row">
						<div className="col-lg-12">
								<SideBarMobile active="1"/>
						</div>
						<div className="col-lg-12 mb10">
							<div className="breadcrumb_content style2">
								<h2 className="breadcrumb_title">Howdy, Ali!</h2>
								<p>We are glad to see you again!</p>
							</div>
						</div>
						<div className="col-sm-6 col-md-6 col-lg-6 col-xl-3">
							<div className="ff_one">
								<div className="icon"><span className="flaticon-home"></span></div>
								<div className="detais">
									<Count number='37'/>
									<p>All Properties</p>
								</div>
							</div>
						</div>
						<div className="col-sm-6 col-md-6 col-lg-6 col-xl-3">
							<div className="ff_one style2">
								<div className="icon"><span className="flaticon-view"></span></div>
								<div className="detais">
									<Count number='24'/>
									<p>Total Views</p>
								</div>
							</div>
						</div>
						<div className="col-sm-6 col-md-6 col-lg-6 col-xl-3">
							<div className="ff_one style3">
								<div className="icon"><span className="flaticon-chat"></span></div>
								<div className="detais">
									<Count number='12'/>
									<p>Total Visitor Reviews</p>
								</div>
							</div>
						</div>
						<div className="col-sm-6 col-md-6 col-lg-6 col-xl-3">
							<div className="ff_one style4">
								<div className="icon"><span className="flaticon-heart"></span></div>
								<div className="detais">
									<Count number='18'/>
									<p>Total Favorites</p>
								</div>
							</div>
						</div>
						<div className="col-xl-7">
							<div className="application_statics">
								<h4>View Statistics</h4>
								<Line
									data={data}
									options={{
										responsive: true,
										title: {
											display: true,
											text: 'Sample tooltip with border'
										},
										tooltips: {
											position: 'nearest',
											mode: 'index',
											intersect: false,
											yPadding: 10,
											xPadding: 10,
											caretSize: 8,
											backgroundColor: 'rgba(72, 241, 12, 1)',
											titleFontColor: '#6F6F6F',
											bodyFontColor: '#6F6F6F',
											borderColor: 'rgba(0,0,0,1)',
											borderWidth: 4
										}}}
									/>
								<div className="c_container"></div>
							</div>
						</div>
						<div className="col-xl-5">
							<div className="recent_job_activity">
								<h4 className="title">Recent Activities</h4>
								<div className="grid">
									<ul>
										<li className="list-inline-item"><div className="icon"><span className="flaticon-home"></span></div></li>
										<li className="list-inline-item"><p>Your listing <strong>Luxury Family Home</strong> has been approved!.</p></li>
									</ul>
								</div>
								<div className="grid">
									<ul>
										<li className="list-inline-item"><div className="icon"><span className="flaticon-chat"></span></div></li>
										<li className="list-inline-item"><p>Kathy Brown left a review  on <strong>Renovated Apartment</strong></p></li>
									</ul>
								</div>
								<div className="grid">
									<ul>
										<li className="list-inline-item"><div className="icon"><span className="flaticon-heart"></span></div></li>
										<li className="list-inline-item"><p>Someone favorites your <strong>Gorgeous Villa Bay View</strong> listing!</p></li>
									</ul>
								</div>
								<div className="grid">
									<ul>
										<li className="list-inline-item"><div className="icon"><span className="flaticon-home"></span></div></li>
										<li className="list-inline-item"><p>Your listing <strong>Luxury Family Home</strong> has been approved!</p></li>
									</ul>
								</div>
								<div className="grid">
									<ul>
										<li className="list-inline-item"><div className="icon"><span className="flaticon-chat"></span></div></li>
										<li className="list-inline-item"><p>Kathy Brown left a review on <strong>Renovated Apartment</strong></p></li>
									</ul>
								</div>
								<div className="grid mb0">
									<ul className="pb0 mb0 bb_none">
										<li className="list-inline-item"><div className="icon"><span className="flaticon-heart"></span></div></li>
										<li className="list-inline-item"><p>Someone favorites your <strong>Gorgeous Villa Bay</strong> View listing!</p></li>
									</ul>
								</div>
							</div>
						</div>
					</div>
					<div className="row mt50">
						<div className="col-lg-6 offset-lg-3">
							<div className="copyright-widget text-center">
								<p>Â© 2020 Find House. Made with love.</p>
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	</section>
    )
}

export default DashboardSection
